#load-ani{
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0; left: 0;
    display: flex; justify-content: center; align-items: center;
}
#load-ani .loader{position: relative;
    width: 120px; height: 120px;;
}

#load-ani .loader .circle{
    position: absolute;
    top: 0; left: 0; width: 100%; height: 100%;
}

#load-ani .loader .circle span{
    position: absolute;
    top: 0; left: 0; width: 15px; height:  15px;
    background: #aaa;
    border-radius: 50%;
    -webkit-animation: circleSize 2s linear infinite;
            animation: circleSize 2s linear infinite;

}

@-webkit-keyframes circleSize{
    0%{transform: scale(1);}
    80%,100%{transform: scale(0);}
}

@keyframes circleSize{
    0%{transform: scale(1);}
    80%,100%{transform: scale(0);}
}
